import React, { useEffect, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { supabase } from '../lib/api';
import { dateConvert } from '../helper';
import { updateWatchlist } from '../store/movies';
import { getWatchlist } from '../store/user';
import { ReactComponent as EyePlusIcon } from '../assets/icons2/w/mdi_eye-plus-outline.svg';
import { ReactComponent as EyeMinusIcon } from '../assets/icons2/w/mdi_eye-minus-outline.svg';
import { ReactComponent as StarPlusIcon } from '../assets/icons2/w/mdi_star-check-outline.svg';
import { ReactComponent as StarMinusIcon } from '../assets/icons2/w/mdi_star-minus-outline.svg';
import { ReactComponent as CalendarPlusIcon } from '../assets/icons2/w/mdi_calendar-plus.svg';
import { ReactComponent as CalendarMinusIcon } from '../assets/icons2/w/mdi_calendar-minus.svg';
import { ReactComponent as Back } from '../assets/icons2/mdi_arrow-left.svg';
import poster from '../assets/images/poster.jpg';
// import Loading from './Loading';
import Message from './Message';
import Loading from './Loading';

const Profile = () => {
  // eslint-disable-next-line
  const [message, setMessage] = useState(null);
  const [iWillSee, setIWillSee] = useState(false);
  const [iAlreadySaw, setIAlreadySaw] = useState(false);
  const [favorite, setFavorite] = useState(false);

  const dispatch = useDispatch();
  let history = useHistory();

  const { data: login } = useSelector((state) => state.user.login);
  const {
    data: watchlist,
    loading: watchlistLoading,
    error: watchlistError,
  } = useSelector((state) => state.user.watchlist);
  const { loading: updateWatchlistLoading } = useSelector(
    (state) => state.user.watchlist
  );
  const back = () => history.goBack();

  const handleDelete = async (id) => {
    if (login && id) {
      // eslint-disable-next-line
      const { data, error } = await supabase
        .from('watchlist')
        .delete()
        .eq('id', id)
        .eq('user_id', login.id);
      if (error) console.log(error);
      await dispatch(getWatchlist(login.id));
    }
  };

  const onChangeWill = async (item_id, value) => {
    await dispatch(updateWatchlist(login?.id, item_id, { i_will_see: !value }));
    setIWillSee(!iWillSee);

    await dispatch(getWatchlist(login?.id));
  };
  const onChangeAlready = async (item_id, value) => {
    await dispatch(
      updateWatchlist(login?.id, item_id, { i_already_saw: !value })
    );
    setIAlreadySaw(!iAlreadySaw);

    await dispatch(getWatchlist(login?.id));
  };
  const onChangeFavorite = async (item_id, value) => {
    await dispatch(updateWatchlist(login?.id, item_id, { favorite: !value }));
    setFavorite(!favorite);

    await dispatch(getWatchlist(login?.id));
  };

  useEffect(() => {
    (async function () {
      if (login) {
        await dispatch(getWatchlist(login.id));
      }
    })();
  }, [login, dispatch]);

  if (watchlistError) <p>Error</p>;
  if (watchlistLoading) <Loading />;
  return (
    <div>
      <button className='btn-inline' onClick={back}>
        <Back /> Back
      </button>
      <div className='App-profile--user'>
        <h2 id='profile'>Profile</h2>
        <div className='p-4'>
          <div>{login?.id}</div>
          <div>
            <h4>{login?.email || 'Anonymous_'}</h4>
            <small>
              {login?.email} - Registration day {dateConvert(login?.created_at)}
            </small>
          </div>
        </div>
      </div>
      <div className='App-watchlist py-4'>
        {!!message && <Message data={message} type='alert-success' />}
      </div>
      <section>
        {watchlist &&
          watchlist.map((i) => (
            <div className='App-profile--watchlist-container' key={i.id}>
              <div className='App-profile--watchlist-poster'>
                <Link to={`../movies/${i.movie_id}`}>
                  <div
                    style={{
                      background: `url(${
                        i.movies.poster || poster
                      }) center top`,
                      height: '60px',
                      width: '45px',
                      backgroundSize: 'cover',
                      backgroundRepeat: 'no-repeat',
                    }}
                  ></div>
                </Link>
              </div>
              <div className='App-profile--watchlist-body m-1'>
                <div>
                  <h4>{i.movies.name}</h4>
                  <small style={{ opacity: '0.75' }}>
                    {i.movies.year} -{' '}
                    {i.movies ? i.movies.type.toUpperCase() : ''}
                  </small>
                </div>

                <div className='App-profile--watchlist-buttons flex flex-justify-between'>
                  <div className='flex wrap gap-0'>
                    {!i.i_will_see ? (
                      <button
                        disabled={updateWatchlistLoading}
                        className='btn-inline  btn-info me-1'
                        onClick={() => onChangeWill(i.movie_id, i.i_will_see)}
                      >
                        <CalendarPlusIcon /> I'll see
                      </button>
                    ) : (
                      <button
                        disabled={updateWatchlistLoading}
                        className='btn-inline btn-error me-1'
                        onClick={() => onChangeWill(i.movie_id, i.i_will_see)}
                      >
                        <CalendarMinusIcon /> I'll see
                      </button>
                    )}
                    {!i.i_already_saw ? (
                      <button
                        disabled={updateWatchlistLoading}
                        className='btn-inline btn-info me-1'
                        onClick={() =>
                          onChangeAlready(i.movie_id, i.i_already_saw)
                        }
                      >
                        <EyePlusIcon /> I already saw
                      </button>
                    ) : (
                      <button
                        disabled={updateWatchlistLoading}
                        className='btn-inline btn-error me-1'
                        onClick={() =>
                          onChangeAlready(i.movie_id, i.i_already_saw)
                        }
                      >
                        <EyeMinusIcon /> I already saw
                      </button>
                    )}
                    {!i.favorite ? (
                      <button
                        disabled={updateWatchlistLoading}
                        className='btn-inline btn-info me-1'
                        onClick={() => onChangeFavorite(i.movie_id, i.favorite)}
                      >
                        <StarPlusIcon /> Favorite
                      </button>
                    ) : (
                      <button
                        disabled={updateWatchlistLoading}
                        className='btn-inline btn-error me-1'
                        onClick={() => onChangeFavorite(i.movie_id, i.favorite)}
                      >
                        <StarMinusIcon /> Favorite
                      </button>
                    )}
                  </div>
                  <div className='flex flex-align-end'>
                    <Link
                      className='btn-inline px-2 me-1'
                      to={`/movies/${i.movies.id}`}
                    >
                      Movie Page
                    </Link>
                    <button
                      onClick={() => handleDelete(i.id)}
                      className='btn-inline btn-outline-error px-2'
                    >
                      Delete
                    </button>
                  </div>
                </div>
              </div>
            </div>
          ))}
      </section>
    </div>
  );
};

export default Profile;
