export class SlideStories {
  constructor(
    id,
    slide,
    items,
    nextButton,
    prevButton,
    thumb,
    title,
    paragraph,
    year,
    link
  ) {
    this.slide = slide;
    this.items = items.childNodes;
    this.nextButton = nextButton;
    this.prevButton = prevButton;
    this.thumb = thumb;

    this.title = title;
    this.paragraph = paragraph;
    this.year = year;
    this.link = link;

    this.active = 0;
    this.init();
  }

  activeSlide(index) {
    this.active = index;

    this.items.forEach((item) => item.classList.remove('active'));
    this.items[index].classList.add('active');

    this.thumbItems.forEach((item) => item.classList.remove('active'));
    this.thumbItems[index].classList.add('active');

    this.titleItems.forEach((item) => item.classList.remove('active'));
    this.titleItems[index].classList.add('active');

    this.pItems.forEach((item) => item.classList.remove('active'));
    this.pItems[index].classList.add('active');

    this.linkItems.forEach((item) => item.classList.remove('active'));
    this.linkItems[index].classList.add('active');

    this.autoSlide();
  }

  next() {
    if (this.active < this.items.length - 1) {
      this.activeSlide(this.active + 1);
    } else {
      this.activeSlide(0);
    }
  }

  prev() {
    if (this.active > 0) {
      this.activeSlide(this.active - 1);
    } else {
      this.activeSlide(this.items.length - 1);
    }
  }

  addNavigation() {
    const nextButton = this.nextButton;
    const prevButton = this.prevButton;

    nextButton.addEventListener('click', this.next);
    prevButton.addEventListener('click', this.prev);
  }

  addThumbItems() {
    this.items.forEach(() => (this.thumb.innerHTML += '<span></span>'));
    this.thumbItems = Array.from(this.thumb.children);
    this.titleItems = Array.from(this.title.childNodes);
    this.pItems = Array.from(this.paragraph.childNodes);
    this.linkItems = Array.from(this.link.childNodes);
  }

  autoSlide() {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(this.next, 5000);
  }

  init() {
    this.next = this.next.bind(this);
    this.prev = this.prev.bind(this);
    // this.items = this.slide.querySelectorAll('.App-slide--items > *');
    // this.t = this.thumb;
    this.addThumbItems();
    this.activeSlide(0);
    this.addNavigation();
  }
}
