import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import { getMovies } from '../store/movies';
import { ReactComponent as Back } from '../assets/icons2/mdi_arrow-left.svg';
import Card from './Card';
import Loading from './Loading';

const Movie = () => {
  const [movies, setMovies] = useState([]);
  const [tvs, setTvs] = useState([]);
  const [showMovie, setShowMovie] = useState(true);
  const [showTv, setShowTv] = useState(false);

  const dispatch = useDispatch();
  const history = useHistory();

  const { data, loading } = useSelector((state) => state.movies.movies);

  const back = () => history.goBack();

  const handleChange = () => {
    setShowMovie(!showMovie);
    setShowTv(!showTv);
  };

  useEffect(() => {
    if (!data) dispatch(getMovies());
    if (data) {
      setMovies(data.filter((i) => i.type === 'movie'));
      setTvs(data.filter((i) => i.type === 'tv'));
    }
  }, [dispatch, data]);

  return (
    <div>
      <button className='btn-inline' onClick={back}>
        <Back /> Back
      </button>
      <div>
        <h2>Movie or Tv show</h2>
        <div className='pb-3' onClick={handleChange}>
          <button
            className={`me-1 btn-inline ${
              showMovie ? 'btn-primary' : 'btn-secondary'
            }`}
          >
            Movies
          </button>
          <button
            className={`btn-inline ${showTv ? 'btn-primary' : 'btn-secondary'}`}
          >
            Tv Show
          </button>
        </div>
        <div className='flex wrap gap-1 flex-justify-center'>
          {loading ? (
            <Loading />
          ) : (
            showMovie &&
            movies.map((m) => (
              <Link to={`movies/${m.id}`} key={m.id} className='flex'>
                <Card
                  name={m.name}
                  storyline={m.storyline}
                  date={m.year}
                  poster={m.poster}
                />
              </Link>
            ))
          )}
          {loading ? (
            <Loading />
          ) : (
            showTv &&
            tvs.map((t) => (
              <Link to={`movies/${t.id}`} key={t.id} className='flex'>
                <Card
                  name={t.name}
                  storyline={t.storyline}
                  date={t.year}
                  poster={t.poster}
                />
              </Link>
            ))
          )}
        </div>
      </div>
    </div>
  );
};

export default Movie;
