import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { getSimilar } from '../store/movies';
import Card from './Card';
import Loading from './Loading';

const Similar = ({ tags }) => {
  const dispatch = useDispatch();
  const { data, loading } = useSelector((state) => state.movies.similar);

  useEffect(() => {
    (async function () {
      if (tags) await dispatch(getSimilar(tags));
    })();
  }, [tags, dispatch]);

  return (
    <div className='mt-4'>
      <h2>Similar</h2>
      <div className='flex wrap gap-1 flex-justify-center'>
        {!loading ? (
          data &&
          data
            .map((i) => (
              <Link to={`../movies/${i.id}`} key={i.id} className='flex'>
                <Card
                  name={i.name}
                  storyline={i.storyline}
                  date={i.year}
                  poster={i.poster}
                />
              </Link>
            ))
            .slice(0, 5)
        ) : (
          <Loading />
        )}
      </div>
    </div>
  );
};

export default Similar;
