import React, { useEffect, useState } from 'react';
import { deleteMovie, getMovies } from '../store/movies';
import { useDispatch, useSelector } from 'react-redux';
import { arrayToString, helperFunction } from '../helper';
import { Link } from 'react-router-dom';
import { ReactComponent as OkIcon } from '../assets/icons2/extra/mdi_check-circle-outline-green.svg';
import { ReactComponent as OkIcon2 } from '../assets/icons2/extra/mdi_check-circle-outline.svg';
import { ReactComponent as DelIcon } from '../assets/icons2/b/mdi_delete-outline.svg';
import { ReactComponent as EditIcon } from '../assets/icons2/b/mdi_clipboard-file-outline.svg';
import { ReactComponent as EyeIcon } from '../assets/icons2/b/mdi_eye-outline.svg';
import { ReactComponent as MovieIcon } from '../assets/icons2/w/mdi_movie-open-outline.svg';
import { ReactComponent as TVIcon } from '../assets/icons2/w/mdi_television-classic.svg';
import Message from './Message';
import Loading from './Loading';

const AddMovieList = ({ loadMovie }) => {
  const dispatch = useDispatch();

  const [helperText, setHelperText] = useState({
    error: false,
    text: '',
    type: '',
  });

  const { data: movies, loading } = useSelector((state) => state.movies.movies);

  const handleDelete = async (id, type) => {
    try {
      await dispatch(deleteMovie(id));
      helperFunction(
        false,
        'Movie successfully deleted.',
        'alert-success',
        setHelperText,
        3000
      );
    } catch (error) {
      helperFunction(true, error.message, 'alert-error', setHelperText, 3000);
    } finally {
      await dispatch(getMovies());
    }
  };

  useEffect(() => {
    (async function () {
      await dispatch(getMovies());
    })();
  }, [dispatch]);

  return (
    <div className='mt-4'>
      <h2>Movie List</h2>

      {!!helperText.text && (
        <Message data={helperText.text} type={helperText.type} />
      )}

      {loading && <Loading />}

      {movies?.length > 0 ? (
        <table className='table-content'>
          <thead>
            <tr>
              <th>id</th>
              <th>name</th>
              <th>year</th>
              <th>storyline</th>
              <th>cast</th>
              <th>tags</th>
              <th>trailer</th>
              <th>image</th>
              <th>poster</th>
              <th>type</th>
              <th>options</th>
            </tr>
          </thead>

          {movies.map((i) => (
            <tbody key={i.id}>
              <tr>
                <td>{i.id.slice(0, 4) + '...'}</td>
                <td>
                  <b>{i.name}</b>
                </td>
                <td>{i.year}</td>
                <td>{i.storyline ? i.storyline.slice(0, 50) + '...' : ''}</td>
                <td>{arrayToString(i.cast)}</td>
                <td>{i.tags && i.tags.join(', ')}</td>
                <td>
                  <div className='flex flex-justify-center'>
                    {i.trailer.length > 0 ? <OkIcon /> : <OkIcon2 />}
                  </div>
                </td>
                <td>
                  <div className='flex flex-justify-center'>
                    {i.image ? (
                      <a target='_blank' rel='noreferrer' href={`${i.image}`}>
                        URL
                      </a>
                    ) : (
                      'null'
                    )}
                  </div>
                </td>
                <td>
                  <div className='flex flex-justify-center'>
                    {i.poster ? (
                      <a target='_blank' rel='noreferrer' href={`${i.poster}`}>
                        URL
                      </a>
                    ) : (
                      'null'
                    )}
                  </div>
                </td>
                <td>
                  <div className='flex flex-justify-center'>
                    {i.type === 'movie' ? <MovieIcon /> : <TVIcon />}
                  </div>
                </td>
                <td>
                  <div className='flex'>
                    <a
                      href='#buttons'
                      className='btn-icon btn-secondary me-1'
                      onClick={() => handleDelete(i.id)}
                    >
                      <DelIcon />
                    </a>
                    <a
                      href='#content'
                      className='btn-icon btn-secondary me-1'
                      onClick={() => loadMovie(i, 'movie')}
                    >
                      <EditIcon />
                    </a>
                    <Link
                      to={`movies/${i.id}`}
                      className='btn-icon btn-secondary'
                    >
                      <EyeIcon />
                    </Link>
                  </div>
                </td>
              </tr>
            </tbody>
          ))}
        </table>
      ) : (
        <Message data='Movies not found' type='alert-info' />
      )}
    </div>
  );
};

export default AddMovieList;
