import React, { useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { SlideStories } from '../helper/slide';
import { compactString } from '../helper';
import { ReactComponent as PrevIcon } from '../assets/icons2/mdi_arrow-left-big.svg';
import { ReactComponent as NextIcon } from '../assets/icons2/mdi_arrow-right-big.svg';
import default_poster from '../assets/images/default_poster.jpg';
import '../css/slide.css';

const Slide = () => {
  //eslint-disable-next-line
  const { data, loading } = useSelector((state) => state.movies.movies);
  const [images, setImages] = useState(null);

  //eslint-disable-next-line
  const [items, setItems] = useState();

  const slideRef = useRef();
  const itemsRef = useRef();
  const nextButtonRef = useRef();
  const prevButtonRef = useRef();
  const thumbRef = useRef();

  const asideLinkRef = useRef();
  const asideTitleRef = useRef();
  const asideParagraphRef = useRef();
  const asideYearRef = useRef();

  useEffect(() => {
    if (data) {
      let i = data.filter((i) => i.image !== '');
      setImages(i);

      new SlideStories(
        '1',
        slideRef.current,
        itemsRef.current,
        nextButtonRef.current,
        prevButtonRef.current,
        thumbRef.current,
        asideTitleRef.current,
        asideParagraphRef.current,
        asideYearRef.current,
        asideLinkRef.current
      );
    }
  }, [data]);

  return (
    <div className='App-slide--container'>
      <div ref={slideRef} className='App-slide'>
        <div className='App-slide--items' ref={itemsRef}>
          <div
            className='App-slide--poster'
            style={{
              background: `url(${
                images && images[0] ? images[0].image : default_poster
              })`,
            }}
          ></div>
          <div
            className='App-slide--poster'
            style={{
              background: `url(${
                images && images[1] ? images[1].image : default_poster
              })`,
            }}
          ></div>
          <div
            className='App-slide--poster'
            style={{
              background: `url(${
                images && images[2] ? images[2].image : default_poster
              })`,
            }}
          ></div>
        </div>

        <div className='App-slide--nav'>
          <div className='App-slide--thumb' ref={thumbRef}></div>
          <button className='App-slide--button-prev' ref={prevButtonRef}>
            <PrevIcon />
          </button>
          <button className='App-slide--button-next' ref={nextButtonRef}>
            <NextIcon />
          </button>
        </div>
      </div>
      {/*  */}
      <div className='App-slide--aside'>
        <div className='App-slide--aside-header py-4'>
          <div ref={asideTitleRef} className='App-slide--aside-title'>
            <div>
              <h1 className='before'>
                {images && images.length > 0 ? images[0].name : 'No title'}
              </h1>
              <small ref={asideYearRef}>
                {images && images.length > 0 ? images[0].year : 'No year'}
              </small>
            </div>
            <div>
              <h1 className='before'>
                {images && images.length > 1 ? images[1].name : 'No title'}
              </h1>
              <small ref={asideYearRef}>
                {images && images.length > 1 ? images[1].year : 'No year'}
              </small>
            </div>
            <div>
              <h1 className='before'>
                {images && images.length > 2 ? images[2].name : 'No title'}
              </h1>
              <small ref={asideYearRef}>
                {images && images.length > 2 ? images[2].year : 'No year'}
              </small>
            </div>
          </div>
        </div>
        <div
          style={{ display: 'flex', flexDirection: 'column', height: '100%' }}
        >
          <div
            style={{ flex: '1', color: '#8d8d8d' }}
            className='App-slide--aside-paragraph'
            ref={asideParagraphRef}
          >
            <p>
              {images && images.length > 0
                ? compactString(images[0].storyline, 150)
                : '2'}
            </p>
            <p>
              {images && images.length > 0
                ? compactString(images[1].storyline, 150)
                : '2'}
            </p>
            <p>
              {images && images.length > 0
                ? compactString(images[2].storyline, 150)
                : '2'}
            </p>
          </div>
          <div className='App-slide--aside-links' ref={asideLinkRef}>
            <Link
              to={`/movies/${images && images.length > 0 ? images[0].id : '/'}`}
              className='btn-inline btn-primary px-4'
            >
              Read more
            </Link>
            <Link
              to={`/movies/${images && images.length > 1 ? images[1].id : '/'}`}
              className='btn-inline btn-primary px-4'
            >
              Read more
            </Link>
            <Link
              to={`/movies/${images && images.length > 2 ? images[2].id : '/'}`}
              className='btn-inline btn-primary px-4'
            >
              Read more
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Slide;
