import { createSlice } from '@reduxjs/toolkit';
import { supabase } from '../lib/api';
import { combineReducers } from '@reduxjs/toolkit';

export const getMoviesSlice = createSlice({
  name: 'movies',
  initialState: {
    loading: false,
    data: null,
    error: null,
  },
  reducers: {
    reqGetMovies: (state) => {
      state.loading = true;
      state.error = null;
    },
    sucGetMovies: (state, action) => {
      state.loading = false;
      state.data = action.payload;
    },
    errGetMovies: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
  },
});

export const getMovieSlice = createSlice({
  name: 'movie',
  initialState: {
    loading: false,
    data: null,
    error: null,
  },
  reducers: {
    reqGetMovie: (state) => {
      state.loading = true;
      state.error = null;
    },
    sucGetMovie: (state, action) => {
      state.loading = false;
      state.data = action.payload;
    },
    errGetMovie: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
  },
});

export const delMovieSlice = createSlice({
  name: 'movie',
  initialState: {
    loading: false,
    error: null,
  },
  reducers: {
    reqDelMovie: (state) => {
      state.loading = true;
      state.error = null;
    },
    sucDelMovie: (state) => {
      state.loading = false;
    },
    errDelMovie: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
  },
});

export const searchMovieSlice = createSlice({
  name: 'search',
  initialState: {
    loading: false,
    data: null,
    error: null,
  },
  reducers: {
    reqSearchMovie: (state) => {
      state.loading = true;
      state.error = null;
    },
    sucSearchMovie: (state, action) => {
      state.loading = false;
      state.data = action.payload;
    },
    errSearchMovie: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
    resetSearchMovie: (state) => {
      state.loading = false;
      state.error = false;
      state.data = null;
    },
  },
});

export const pageViewIncSlice = createSlice({
  name: 'views',
  initialState: {
    loading: false,
    data: null,
    error: null,
  },
  reducers: {
    reqPageView: (state) => {
      state.loading = true;
      state.error = null;
    },
    sucPageView: (state, action) => {
      state.loading = false;
      state.data = action.payload;
    },
    errPageView: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
  },
});

export const getSimilarSlice = createSlice({
  name: 'similar',
  initialState: {
    loading: false,
    data: null,
    error: null,
  },
  reducers: {
    reqSimilar: (state) => {
      state.loading = true;
      state.error = null;
    },
    sucSimilar: (state, action) => {
      state.loading = false;
      state.data = action.payload;
    },
    errSimilar: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
  },
});

export const updateWatchlistSlice = createSlice({
  name: 'update-watchlist',
  initialState: {
    loading: false,
    data: null,
    error: null,
  },
  reducers: {
    reqUpdateWatchlist: (state) => {
      state.loading = true;
      state.error = null;
    },
    sucUpdateWatchlist: (state, action) => {
      state.loading = false;
      state.data = action.payload;
    },
    errUpdateWatchlist: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
  },
});

const reducer = combineReducers({
  movies: getMoviesSlice.reducer,
  movie: getMovieSlice.reducer,
  search: searchMovieSlice.reducer,
  views: pageViewIncSlice.reducer,
  similar: getSimilarSlice.reducer,
  updateWatchlist: updateWatchlistSlice.reducer,
});

const { reqGetMovies, sucGetMovies, errGetMovies } = getMoviesSlice.actions;
const { reqGetMovie, sucGetMovie, errGetMovie } = getMovieSlice.actions;
const { reqDelMovie, sucDelMovie, errDelMovie } = delMovieSlice.actions;
const { reqSearchMovie, sucSearchMovie, errSearchMovie, resetSearchMovie } =
  searchMovieSlice.actions;
const { sucPageView, errPageView } = pageViewIncSlice.actions;
const { errSimilar, reqSimilar, sucSimilar } = getSimilarSlice.actions;
const { errUpdateWatchlist, sucUpdateWatchlist, reqUpdateWatchlist } =
  updateWatchlistSlice.actions;

export default reducer;

// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
export const getMovies = () => async (dispatch) => {
  await dispatch(reqGetMovies());

  let { data, error } = await supabase
    .from('movies')
    .select('*')
    .order('created_at', { ascending: false });

  if (error) errGetMovies(error);
  else await dispatch(sucGetMovies(data));
};
// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
export const getMovie = (id) => async (dispatch) => {
  await dispatch(reqGetMovie());

  let { data, error } = await supabase.from('movies').select('*').eq('id', id);

  if (error) errGetMovie(error);
  else await dispatch(sucGetMovie(data[0]));
};
// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
export const deleteMovie = (id) => async (dispatch) => {
  await dispatch(reqDelMovie());
  const { error } = await supabase.from('movies').delete().eq('id', id);
  if (error) errDelMovie(error);
  else dispatch(sucDelMovie());
};
// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
export const searchMovie = (term) => async (dispatch) => {
  await dispatch(reqSearchMovie());

  let { data, error } = await supabase
    .from('movies')
    .select('*')
    .ilike('name', `%${term}%`);

  if (error) errSearchMovie(error);
  else await dispatch(sucSearchMovie(data));
};

export const resetSearch = () => async (dispatch) => {
  await dispatch(resetSearchMovie());
};

// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
export const pageViewInc = (id) => async (dispatch) => {
  let { data, error } = await supabase
    .from('movies')
    .select('*')
    .eq('id', id)
    .single();

  if (data) {
    await supabase
      .from('movies')
      .update({ views: data.views + 1 })
      .eq('id', id);
  }

  if (error) errPageView(error);
  else await dispatch(sucPageView(data));
};
// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
export const getSimilar = (tags) => async (dispatch) => {
  await dispatch(reqSimilar());
  let { data, error } = await supabase
    .from('movies')
    .select('*')
    .contains('tags', [tags]);

  if (error) dispatch(errSimilar(error));
  else await dispatch(sucSimilar(data));
};
// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
export const updateWatchlist =
  (login_id, item_id, body) => async (dispatch) => {
    await dispatch(reqUpdateWatchlist());
    const { data, error } = await supabase
      .from('watchlist')
      .update(body)
      .eq('user_id', login_id)
      .eq('movie_id', item_id);
    if (error) console.log(error);

    if (error) dispatch(errUpdateWatchlist(error));
    else await dispatch(sucUpdateWatchlist(data));
  };
